$('.basket_form_btn').on('click', function () {
    let select_city = $('.select_city').val();
    let departament;
    let wrapper = $('.basket_form__block');
    let error = 0;
    let method_name = $('.delivery_select').find('.delivery_method_profile.active').attr('data-name');
    let payment_name = $('.payment_select').find('.delivery_method_profile.active').attr('data-name');
    let method = $('.delivery_method_profile.active').attr('data-method');
    let price = $('.basket_all_payment').text().replace(/\s+/g, '');
    if (method == 'curier') {
        departament = $(wrapper).find('.address').val();
    } else if (method == 'nova_pochta') {
        departament = $('.select_department').val();
    } else if (method == 'self') {
        departament = 'клієнт обрав самовивіз';
    }

    let body = {
        "name": $(wrapper).find('.name').val(),
        "email": $(wrapper).find('.email').val(),
        "phone": $(wrapper).find('.phone').val(),
        "comments": $(wrapper).find('.text').val() == '' ? 'клієнт не залишив коментаря' : $(wrapper).find('.text').val(),
        "city": select_city,
        "address": `${departament}, ${method_name}`,
        "payment_opt": payment_name,
        "price": price,
    }

    $.each(body, function (index, value) {
        if (value == undefined || value == '') {
            error++;
        }
    });

    console.log('body: ', body);
    if (error != 0) {
        $('.error_payment__block').text('Заповніть усі поля!');

    } else {
        $('.error_payment__block').text('');
        fetch('/api/order_items/', {
            method: 'POST',
            body: JSON.stringify(body),
            headers: {
                "Content-Type": "application/json",
                "Accept": "application/json"
            }
        })
            .then(data => {
                return data.json();
            })
            .then(data => {
                console.log('data: ', data);
                window.location = data.url;

            });
    }

});




$('.basket_product_profile_quantity_plus').on('click', function () {
    let value = plus('.basket_product_profile', '.quantity_input', this);
    let wrapper = $(this).parents('.basket_product_profile');
    let item_id = Number($(wrapper).attr('data-quantity_item_id'));
    change_quantity(item_id, value, wrapper);
});

$('.basket_product_profile_quantity_minus').on('click', function () {
    let value = minus('.basket_product_profile', '.quantity_input', this);
    let wrapper = $(this).parents('.basket_product_profile');
    let item_id = Number($(wrapper).attr('data-quantity_item_id'));
    change_quantity(item_id, value, wrapper);

});
$('.quantity_input').on('change', function () {
    let value = input_basket(this);
    let wrapper = $(this).parents('.basket_product_profile');
    let item_id = Number($(wrapper).attr('data-quantity_item_id'));
    change_quantity(item_id, value, wrapper);
});

$('.basket_product_profile_delete').on('click', function () {
    let wrapper = $(this).parents('.basket_product_profile');
    let item_id = Number($(wrapper).attr('data-quantity_item_id'));


    fetch(`/api/cart_item/${item_id}`, {
        method: 'DELETE',
    })
        .then(data => {
            return data.json();
        })
        .then(data => {
            delete_item(this, '.basket_product_profile');
            change_counter(data.cart_items_quantity, null);
            let length_item = $('.basket_product_profile_all_value').length;
            let current_price = Number($(wrapper).find('.basket_product_profile_all_value').find('span').text());
            let all_price;
            if (length_item == 1) {
                all_price = 0;
            } else {
                all_price = return_all_price() - current_price;
            }




            $('.basket_all_payment').text(`${all_price} ${data.cart_currency}`);
        });
});

$('.delivery_method_profile').on('click', function () {
    let wrapper = $(this).parents('.delivery_method__block');
    let method = $(this).attr('data-method');
    $(wrapper).find('.delivery_method_profile').removeClass('active');
    $(this).addClass('active');

    // я був задовбаний, писав цей код вночі, придумувати щось краще було лєнь
    if (method == 'curier') {
        $('.input__wrap_departament').css('display', 'none');
        $('.input__wrap_address').css('display', 'flex');
    } else if (method == 'nova_pochta') {
        $('.input__wrap_departament').css('display', 'block');
        $('.input__wrap_address').css('display', 'none');
    } else if (method == 'self') {
        $('.input__wrap_address').css('display', 'none');
        $('.input__wrap_departament').css('display', 'none');
    }
})


function change_quantity(item_id, value, wrapper) {
    fetch(`/api/cart_item/${item_id}/`, {
        method: 'PATCH',
        body: JSON.stringify({
            quantity: value,
        }),
        headers: {
            "Content-Type": "application/json",
            "Accept": "application/json"
        },
    })
        .then(data => {
            return data.json();
        })
        .then(data => {
            change_counter(data.cart_items_quantity, null);
            $(wrapper).find('.basket_product_profile_all_value').find('span').text(`${data.cart_item_total_price}`);
            let all_price = return_all_price();
            $('.basket_all_payment').text(`${all_price} ${data.cart_currency}`);
            // $('.basket_all_sale_payment').text(`${data.cart_total_price} ${data.cart_currency}`);
        });
}


function return_all_price() {
    let all_item = $('.basket_product_profile_all_value').find('span');
    let all_price = 0;
    $.each(all_item, function (index, value) {
        let price = Number($(value).text());
        all_price += price;
    });


    return all_price;
}