var burger = $('.menu-trigger');

burger.on('click', function (e) {
    e.preventDefault();
    $(this).toggleClass('active');
    $('.header').toggleClass('active');


    if ($(this).hasClass("active")) {
        $("html,body").css("overflow", "hidden");
    } else {
        $("html,body").css("overflow", "visible");
    }
})



